@charset 'UTF-8';
@import 'colors';
@import 'variables';
@import 'typography';
@import 'components/skeleton';
@import 'breakpoints';

@font-face {
  /* Not implemented yet but is a nice solution for async loading fonts
     https://tabatkins.github.io/specs/css-font-display/
  font-display: optional; */
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;

  /* Using font in base64 format to avoid blinking text rendering on Chrome, Firefox is ok
  src: url('themes/default-theme/fonts/Roboto-normal-400.woff') format('woff'); */
  src: url('data:application/font-woff;base64,') format('woff');
}

body {
  /* Remove default margin is currently needed for iOS viewer embeds. */
  margin: 0;

  /* Text adjust is set to 100% to avoid iOS Safari bugs where the font-size is
     sometimes not restored during orientation. See #449. */
  text-size-adjust: 100%;
}
